@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700&display=swap);

.Notification_Header body {
    font-family: "Roboto", sans-serif;
    background: #EFF1F3;
    min-height: 100vh;
    position: relative;
}

.Notification_Header .section-50 {
    padding: 50px 0;
}

.Notification_Header .m-b-50 {
    margin-bottom: 50px;
}

.Notification_Header .dark-link {
    color: #333;
}

.Notification_Header .heading-line {
    position: relative;
    padding-bottom: 5px;
}

.Notification_Header .heading-line:after {
    content: "";
    height: 4px;
    width: 75px;
    background-color: #29B6F6;
    position: absolute;
    bottom: 0;
    left: 0;
}

.Notification_Header .notification-ui_dd-content {
    margin-bottom: 30px;
}

.Notification_Header .notification-list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding: 20px;
    margin-bottom: 7px;
    background: #fff;
    -webkit-box-shadow: 0 3px 10px rgba(0, 0, 0, 0.06);
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.06);
}

.Notification_Header .notification-list--unread {
    border-left: 2px solid #29B6F6;
}

.Notification_Header .notification-list .notification-list_content {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

.Notification_Header .notification-list .notification-list_content .notification-list_img img {
    height: 48px;
    width: 48px;
    border-radius: 50px;
    margin-right: 20px;
}

.Notification_Header .notification-list .notification-list_content .notification-list_detail p {
    margin-bottom: 5px;
    line-height: 1.2;
}

.Notification_Header .notification-list .notification-list_feature-img img {
    height: 48px;
    width: 48px;
    border-radius: 5px;
    margin-left: 20px;
}